import Vue from 'vue'
import VueRouter from 'vue-router'
import AppMain from '@/views/AppMain'
import AppAddress from '@/views/AppAddress'
import AppSignIn from '@/views/AppSignIn'
import AppCarReserve from '@/views/AppCarReserve'

import AppReserve from '@/views/AppReserve'
import AppReturn from '@/views/AppReturn'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'AppMain',
    component: AppMain
  },
  {
    path: '/address',
    name: 'AppAddress',
    component: AppAddress
  },
  {
    path: '/signin',
    name: 'AppSignIn',
    component: AppSignIn
  },
  {
    path: '/carreserve',
    name: 'AppCarReserve',
    component: AppCarReserve
  },
  {
    path: '/reserve',
    name: 'AppReserve',
    component: AppReserve
  },
  {
    path: '/return',
    name: 'AppReturn',
    component: AppReturn
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
